"use client";
import useSWR from 'swr';

type Contact = { name: string; phone?: string; email?: string; hours?: string; locationUrl?: string };

const fetcher = (url: string) => fetch(url).then(r => r.json());

export default function ResourcesCard({ onOpen }: { onOpen: () => void }) {
  const { data } = useSWR<{ contacts: Contact[] }>("/api/resources", fetcher);
  const contacts = data?.contacts || [];
  
  return (
    <div className="p-4 bg-white rounded border">
      <div className="flex items-center justify-between">
        <h3 className="font-semibold">Counseling Resources</h3>
        <button className="text-sm underline" onClick={onOpen}>I viewed this</button>
      </div>
      <div className="mt-2 space-y-3">
        {contacts.map((c, idx) => (
          <div key={idx} className="text-sm">
            <div className="font-medium">{c.name}</div>
            {c.hours ? <div className="text-gray-600">{c.hours}</div> : null}
            <div className="mt-1 flex gap-2 flex-wrap">
              {c.phone ? (
                <a className="px-2 py-1 bg-blue-100 text-blue-700 rounded" href={`tel:${c.phone}`}>Call now</a>
              ) : null}
              {c.email ? (
                <a className="px-2 py-1 bg-green-100 text-green-700 rounded" href={`mailto:${c.email}`}>Email CAPS</a>
              ) : null}
              {c.locationUrl ? (
                <a className="px-2 py-1 bg-gray-100 rounded" href={c.locationUrl} target="_blank" rel="noreferrer">Visit site</a>
              ) : null}
            </div>
          </div>
        ))}
        {contacts.length === 0 ? <p className="text-sm text-gray-500">No resources loaded.</p> : null}
      </div>
    </div>
  );
}
