"use client";
import { useState } from 'react';

export default function OutreachModal({ open, onClose, sessionId }: { open: boolean; onClose: () => void; sessionId: string }) {
  const [form, setForm] = useState({ name: '', email: '', phone: '', studentId: '', preferredTime: '' });
  const [submitting, setSubmitting] = useState(false);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setSubmitting(true);
    try {
      const res = await fetch('/api/outreach', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ sessionId, consent: true, ...form }) });
      if (res.ok) {
        alert('Outreach request sent successfully.');
        onClose();
      }
      else alert('Unable to send outreach.');
    } catch (err) {
      alert('Error sending outreach request.');
    }
    setSubmitting(false);
  }

  if (!open) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4">
        <h2 className="text-xl font-bold mb-4">Request Counseling Outreach</h2>
        <p className="text-sm text-gray-600 mb-4">
          By submitting this form, you consent to having a counseling professional reach out to you based on the information provided.
        </p>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">Name (optional)</label>
            <input type="text" value={form.name} onChange={(e) => setForm({ ...form, name: e.target.value })} className="w-full border rounded px-3 py-2" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Email (optional)</label>
            <input type="email" value={form.email} onChange={(e) => setForm({ ...form, email: e.target.value })} className="w-full border rounded px-3 py-2" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Phone (optional)</label>
            <input type="tel" value={form.phone} onChange={(e) => setForm({ ...form, phone: e.target.value })} className="w-full border rounded px-3 py-2" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Student ID (optional)</label>
            <input type="text" value={form.studentId} onChange={(e) => setForm({ ...form, studentId: e.target.value })} className="w-full border rounded px-3 py-2" />
          </div>
          <div>
            <label className="block text-sm font-medium mb-1">Preferred time to be contacted (optional)</label>
            <input type="text" value={form.preferredTime} onChange={(e) => setForm({ ...form, preferredTime: e.target.value })} className="w-full border rounded px-3 py-2" placeholder="e.g., weekday mornings" />
          </div>
          <div className="flex gap-2">
            <button type="submit" disabled={submitting} className="flex-1 bg-blue-600 text-white py-2 px-4 rounded disabled:opacity-50">
              {submitting ? 'Sending...' : 'Submit Request'}
            </button>
            <button type="button" onClick={onClose} className="flex-1 bg-gray-300 text-gray-700 py-2 px-4 rounded">
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
